import { createContext, useReducer, useEffect } from 'react'

const notificationReducer = (state, action) => {

    switch (action.type) {
        case 'LIKE':
            state = 'you liked a blog ! '
            return state
        case 'CREATE':
            state = 'reseptisi lisäys onnistui ! :) '
            return state
        case 'CREATE-USER':
            state = 'käyttäjän luominen onnistui ! :) '
            return state
        case 'DELETE':
            state = 'you deleted a blog ! '
            return state
        case 'LOGIN':
            state = `Kiva nähdä sinua, ${action.username} ! :)`
            return state
        case 'LOGIN-ERROR':
            state = `ERROR: väärät tunnukset ! :( \n ${action.message}`
            return state
        case 'CREATE-ERROR':
            state = `ERROR: reseptin lisäys epäonnistui ! :( \n ${action.message}`;
            return state
        case 'CREATE-USER-ERROR':
            state = `ERROR: käyttäjän luominen epäonnistui ! :( \n ${action.error}`;
            return state
        case 'ERROR3':
            state = 'ERROR: liking blog didnt pass thru ! :( '
            return state
        case 'CLEAR':
            return null
        default:
            return state
    }
}

const NotificationContext = createContext()

export const NotificationContextProvider = (props) => {
    const [notification, notificationDispatch] = useReducer(notificationReducer, null)

    useEffect(() => {
        // Clear notification after 5 seconds
        if (notification !== null) {
            const timer = setTimeout(() => {
                notificationDispatch({ type: 'CLEAR' });
            }, 3000);

            return () => {
                clearTimeout(timer);
            };
        }
    }, [notification]);

    return (
        <NotificationContext.Provider value={[notification, notificationDispatch]}>
            {props.children}
        </NotificationContext.Provider>
    )
}

export default NotificationContext