import { Box, Form, Text, TextInput, } from "grommet"
import { Search as SearchIcon } from 'grommet-icons';

const BrowseRecipeForm = ({ searchTerm, handleSearchTerm }) => {
  
  return (
      <Box margin="40px" background="rgba(255, 255, 255, 0.5)"  round="large"  >
        <Form >
          <Box background="background-contrast" round="xsmall" width="medium"  >
            <TextInput
              aria-label="search"
              icon={<SearchIcon color="black"/>}
              size="xxsmall"
              placeholder="Hakukenttä"
              plain
              reverse
              value={searchTerm}
              onChange={handleSearchTerm}
            />
          </Box>
        </Form>
      </Box>
      
  )
}


export default BrowseRecipeForm