import { ResponsiveContext } from "grommet";
import SingleRecipeBigScreen from "./BigScreen";
import SingleRecipeSmallScreen from "./SmallScreen";
import { CenterBox, CenteredContainer } from "styles/styles";
import { useContext } from "react";

const SingleRecipe = ({ recipe }) => {

  const screenSize = useContext(ResponsiveContext);
  const isSmallScreen = ['xsmall', 'small'].includes(screenSize);

  return (
    <CenteredContainer>
      <CenterBox >
      {isSmallScreen 
      ? ( <SingleRecipeSmallScreen {...{
        isSmallScreen, recipe
      }} /> 
      )
      : ( <SingleRecipeBigScreen {...{
            isSmallScreen, recipe
          }} /> )
      }
      </CenterBox>
    </CenteredContainer >
  )
}

export default SingleRecipe