import React from "react";
import styled from "styled-components";
import SearchPartSmallScreen from "./SearchPart/SearchPartSmallScreen";
import BrowseRecipeForm from "./SearchPart/components/BrowseRecipeForm";
import RecipesPartSmallScreen from "./RecipesPart/RecipesPartSmallScreen";
import { Box, Button, Text } from "grommet";
import { Previous } from "grommet-icons";


const ButtonBox = styled(Box)`
    margin-top: 10px;
    margin-bottom: 25px;
    align-items: center;
    background-color: white;
    width: 50px;
    position: flex;
    border-radius: 10%;
` 

const RecipesPageSmallScreen = ({ handleResetButton, handleSearchButton, searchTerm, handleSearchTerm,
  categoryTerm, setCategoryTerm, mainIngredientTerm, setMainIngredientTerm, dietTerm, setDietTerm, speedTerms,
  setSpeedTerms, filterObject, filteredRecipes, setFilteredRecipes, pageVisibility, setPageVisibility

}) => {

  const handleNewSearchButton = () => setPageVisibility(true)

  return (
    <Box>
      {pageVisibility

        ? (
          <Box>
            <Box direction="row">
              <BrowseRecipeForm searchTerm={searchTerm} handleSearchTerm={handleSearchTerm} />
            </Box>
            <SearchPartSmallScreen
              {...{
                searchTerm, handleSearchTerm, categoryTerm, setCategoryTerm,
                mainIngredientTerm, setMainIngredientTerm, dietTerm, setDietTerm,
                speedTerms, setSpeedTerms, filterObject, handleSearchButton, handleResetButton
              }}
            /> </Box>)

        : (

          <Box direction="column" gap="5px" justify="center" align="center">
            <ButtonBox onClick={handleNewSearchButton}>
              <Button ><Previous color="black" /></Button>
            </ButtonBox>
            <RecipesPartSmallScreen {...{ filteredRecipes, setFilteredRecipes }} />
          </Box>)

      }
    </Box>

  )
}

export default RecipesPageSmallScreen