import { Box, Text, TextInput} from "grommet";

const NewRecipeName = ({ newReceptName, handleReceptName, isSmallScreen }) => { 

  console.log('newrecipename', isSmallScreen)
  return (<Box gridArea="newrecept" direction="row" gap="10px" >
    {!isSmallScreen && <Text margin={{ top: "10px" }} size="xxsmall"> Reseptin nimi </Text>}
    <Box background="background-contrast" round="xsmall" width={isSmallScreen ? "small" : "medium"} >
      <TextInput
        aria-label="search"
        size="xsmall"
        placeholder={isSmallScreen ? "Reseptin nimi" : ""}
        plain
        reverse
        value={newReceptName}
        onChange={handleReceptName}
        height="3px"
      />
    </Box>
  </Box>)
}


export default NewRecipeName