import React from "react";
import { useState, useContext } from "react"
import { CenterBox, CenteredContainer } from "styles/styles";
import { v4 as uuidv4 } from 'uuid';
import { Box, Button, Grid, ResponsiveContext } from "grommet";
import ProfileImage from "./ProfileImage";
import ProfileInput from "./ProfileInput";


function randomImgNameGenerator() {
  return uuidv4(); // Generate random UUID
}

const CreateUser = ({ handleUserAdd, handleImageAdd }) => {

  const screenSize = useContext(ResponsiveContext);
  const isSmallScreen = ['xsmall', 'small'].includes(screenSize);

  const [file, setFile] = useState(null);
  const [imageSrc, setImageSrc] = useState('')
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [password2, setPassword2] = useState('')

  const handleSubmit = (event) => {
    event.preventDefault()

    const awsImageName = randomImgNameGenerator()

    handleImageAdd({ file, imageSrc, awsImageName })

    handleUserAdd({
      username: username,
      password: password,
      imageName: awsImageName
    })

    setFile(null)
    setImageSrc('')
    setUsername('')
    setPassword('')
    setPassword2('')
  }

  return (
    <CenteredContainer>
      <CenterBox >
        <Grid
          margin={"15px"}
          fill
          rows={["auto", "flex"]}
          columns={["auto", "flex"]}
          areas={[
            ["fileInput", "profileInput"],
            ["submit", "submit"],
          ]}
        >

          <Box justify="center" direction="row">
            <Box gridArea='fileInput'>
              <ProfileImage {...{ file, setFile, imageSrc, setImageSrc, isSmallScreen }} />
            </Box>

            <Box gridArea='profileInput' >
              <ProfileInput {...{ username, setUsername, password, setPassword, password2, setPassword2, isSmallScreen }} />
            </Box>
          </Box>

          <Box gridArea="submit" align="center" margin="20px">
            <Button label="luo käyttäjä" size="large" color="green" onClick={handleSubmit} />
          </Box>

        </Grid>
      </CenterBox >
    </CenteredContainer >

  )
}

export default CreateUser