import { Box, Grid } from "grommet"
import Selectors from "./components/Selectors";
import Selectors2 from "./components/Selectors2";
import NewRecipeName from "./components/NewRecipeName";
import AddFileForm from "./components/AddFileForm";

import IngredientsTable from "./components/IngredientsForm";
import Instructions from "./components/Instructions";
import ResetSubmit from "./components/ResetSubmit";


const AddRecipeSmallScreen = ({ handleReceptName, newReceptName, setInstructions, instructions, diets, setDiets, recipespeed, handleReceptSpeed, category,
  ingredients, setCategory, mainIngredient, setMainIngredient, handleReset, handleSubmit, file, setFile, imageSrc, setImageSrc, isSmallScreen }) => {
  return (
    <Box margin="5px" >
      <Grid
        margin={"2px"}
        gap="5px"
        fill
        rows={["auto", "flex"]}
        columns={["auto", "flex"]}
        areas={[
          ["fileInput", "name"],
          ["selectors2", "selectors"],
          ["ingredients", "ingredients"],
          ["resetsubmit", "resetsubmit"],
        ]}
      >
        <Box gridArea="fileInput" background="grey" width="200px" height="120px" >
          <AddFileForm file={file} setFile={setFile} imageSrc={imageSrc} setImageSrc={setImageSrc} />
        </Box>

        <Box gridArea="name" align="end" alignContent="center" >
          <NewRecipeName {...{ handleReceptName, newReceptName, isSmallScreen }} />
          <Instructions {...{ instructions, setInstructions }} />
        </Box>


        <Box gridArea="selectors" alignContent="center" direction="row"   >
          <Selectors diets={diets} setDiets={setDiets} isSmallScreen={isSmallScreen} />
        </Box>

        <Box gridArea="selectors2" alignContent="center" direction="row"   >
          <Selectors2 {...{ category, setCategory, mainIngredient, setMainIngredient, recipespeed, handleReceptSpeed, isSmallScreen }} />
        </Box>

        <Box gridArea="ingredients"  >
          <IngredientsTable ingredients={ingredients} isSmallScreen={isSmallScreen} />
        </Box>

        <Box gridArea="resetsubmit" margin="20px">
          <ResetSubmit {...{ handleReset, handleSubmit }} />
        </Box>

      </Grid>
    </Box>

  )
}

export default AddRecipeSmallScreen