import { Box, Card } from "grommet";
import nomilk from 'icons/no-milk.png'
import vegan from 'icons/vegan.png'
import glutenfree from 'icons/gluten-free.png'
import styled from "styled-components";

const StyledBox = styled(Box)`
  border: solid;
  border-color: grey;
  border-width: large;
  background: white;
  border-radius: 20px;
  `

const PhotoBox = styled(Card)`
  margin: 5px;
  border: ${({
  // @ts-ignore
  isSelected }) => isSelected ? '2px solid black' : 'none'};
  round: large;
  `

const Selectors = ({ diets, setDiets, isSmallScreen }) => {

  const photoSize = isSmallScreen ? "35px" : "70px"

  const nomilkLogo = <img className="logo" src={nomilk} alt="logo" width={photoSize} height={photoSize} />
  const veganLogo = <img className="logo" src={vegan} alt="logo" width={photoSize} height={photoSize}/>
  const glutenfreeLogo = <img className="logo" src={glutenfree} alt="logo" width={photoSize} height={photoSize} />

  const handleAdd = (value) => {
    if (diets.includes(value)) {
      var index = diets.indexOf(value);

      if (index !== -1) {
        diets.splice(index, 1);
      }
    }
    else (diets.push(value))

    setDiets([...diets])
  }

  return (
    <Box >
      <StyledBox direction="row" gap="3px" >
        <PhotoBox isSelected={diets.includes('maidoton')} onClick={() => handleAdd('maidoton')} >
          {nomilkLogo}
        </PhotoBox>
        <PhotoBox isSelected={diets.includes('vegaaninen')} onClick={() => handleAdd('vegaaninen')}  >
          {veganLogo}
        </PhotoBox>
        <PhotoBox isSelected={diets.includes('gluteeniton')} onClick={() => handleAdd('gluteeniton')}  >
          {glutenfreeLogo}
        </PhotoBox>
      </StyledBox>
      </Box>
  )
}


export default Selectors