import { Box, FileInput, Image } from "grommet";
import styled from "styled-components";

const StyledBox = styled(Box)`
    width: auto;
    height: auto;
    background: grey;
    border-radius: 2px;
    overflow: hidden;
`
const StyledImage = styled(Image)`
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
    margin: auto;
`

const AddFileForm = ({ file, setFile, setImageSrc, imageSrc }) => {

  return (
    <StyledBox >
       <Box >
        <FileInput
          name="file"
          maxSize={5000}
          disabled={false}
          multiple={false}
          onChange={event => {
            const fileList = event.target.files;
            setFile(fileList[0]); // Set the selected file to state
            setImageSrc(URL.createObjectURL(fileList[0])); // Set the URL of the image to state
          }}
        /></Box>
      
        <Box >
          {file && <StyledImage src={imageSrc} />} {/* Display the image using the src attribute */}
        </Box>
    </StyledBox>
  )
}

export default AddFileForm