import { Box, Text } from "grommet"
import RecipeListed from "pages/RecipesPage/RecipesPart/RecipeListed";
import styled from "styled-components";


const StyledBox = styled(Box)`
  zoom: 30%;
  margin: 10px;
  align-items: center;
  background: light-1
`

const NewestRecipes = ({ recipes }) => {

  const sortedRecipes = recipes.sort((b, a) => {
    return a.created.localeCompare(b.created);
  });

  return (
    <Box margin="10px" >
      <Box> <Text> Uusimmat reseptit </Text></Box>
    {sortedRecipes.slice(0, 5).map(recipe =>
      
      <StyledBox key={recipe.id}>
        <RecipeListed recipe={recipe} />
      </StyledBox>)}

      </Box>
      
  )
}


export default NewestRecipes