import { useContext } from 'react'
import NotificationContext from '../context/NotificationContext'
import styled from 'styled-components';

import {
  Box,
  Text
} from 'grommet';


const StyledText = styled(Text)`
    padding: 10px;
    border: notification !== null ? 'solid 2px black' : null;
    color: black;
    fontWeight: 600;
    background: backgroundcolor;
    width: auto;
    align: center;
    justify: center;
`

const NotificationPop = () => {

  const [notification] = useContext(NotificationContext)

  const isError = notification && notification.startsWith('ERR')
  const backgroundcolor = isError ? 'red' : 'lightgreen'


  return (
      
      <Box background={backgroundcolor} width="auto" alignContent='center'>
        <StyledText >{notification}</StyledText>
      </Box>


)}

        export default NotificationPop