import { Box, TextInput } from "grommet";

const ProfileInput = ({ username, setUsername, password, password2, setPassword, setPassword2, isSmallScreen}) => {
  return (
    <Box margin={isSmallScreen ? '5px' : "50px"} width={isSmallScreen ? '175px' :"500px"} gap={isSmallScreen ? '5px' : "20px"}>
      <Box >
        username
        <TextInput
          type="text"
          size={isSmallScreen ? 'xsmall' : "medium"}
          id='username'
          value={username}
          name="Username"
          onChange={({ target }) => setUsername(target.value)}
        />
      </Box>

      <Box >
        password
        <TextInput
          type="password"
          size={isSmallScreen ? 'xsmall' : "medium"}
          id='password'
          value={password}
          name="Password"
          onChange={({ target }) => setPassword(target.value)}
        />
    
        </Box>

        <Box>
        repeat your password
        <TextInput
          type="password"
          size={isSmallScreen ? 'xsmall' : "medium"}
          id='password2'
          value={password2}
          name="Password2"
          onChange={({ target }) => setPassword2(target.value)}
        />
        </Box>
      </Box>
    
  )
}

export default ProfileInput
