import UserContext from 'context/userContext';
import { Box, Menu, Header, ResponsiveContext } from 'grommet';
import { Menu as MenuIcon, User, Logout, Home } from 'grommet-icons'
import { useContext } from 'react';
import { useNavigate, NavLink } from "react-router-dom";
import styled from 'styled-components';
import BigMenu from './BigMenu';
import ShortMenu from './ShortMenu';
import keijonnopeet from 'icons/keijonnopeet.png'

const StyledHeader = styled(Header)`
  padding-left: medium;
  padding-right: small;
  padding-top: small;
  padding-bottom: small;
  margin: 15px;
  background: ${(props) => props.theme.global.colors.background};
  width: small;
`;

const NavBar = ({ loggedUserData }) => {

  const [user, userDispatch] = useContext(UserContext)
  const navigate = useNavigate();

  const screenSize = useContext(ResponsiveContext);
  const isSmallScreen = ['xsmall', 'small'].includes(screenSize);

  const keijonnopeetLogo = <img className="logo" src={keijonnopeet} alt="logo" width="300px" />

  const handleLogOut = () => {
    window.localStorage.clear()
    userDispatch({
      type: 'LOGOUT',
      payload: null,
    })
    navigate('/')
  }

  const itemsLoggedIn = [
    { label: 'Reseptit', onClick: () => navigate('/recipes') },
    { label: 'Lisää resepti', onClick: () => navigate('/addrecipe') },
    { label: 'Meistä', onClick: () => navigate('/us') },
  ]

  const itemsNoUser = [
    { label: 'Reseptit', onClick: () => navigate('/recipes') },
    { label: 'Meistä', onClick: () => navigate('/us') },
    { label: 'Kirjaudu', onClick: () => navigate('/signin') },
  ]

  const items = user ? itemsLoggedIn : itemsNoUser;

  return (
    <StyledHeader>
      <Box direction="row" justify="between" margin={{right: "50px", top: "35px"}}>
        <Box>{keijonnopeetLogo}</Box>
        {isSmallScreen ? (
          <Box direction='row' gap="7px" >
            <NavLink to="/">
              <Home/>
            </NavLink>
            <Menu margin={{bottom: "15px"}}
              label={<MenuIcon />}
              items={items}
            />
            {user &&  <Box direction='row' gap='15px' ><User onClick={() => navigate('profile')}/> <Logout onClick={handleLogOut}/> </Box> }
         
          </Box>
        ) : (
          <Box>
            {user ? (
              <BigMenu loggedUserData={loggedUserData} handleLogOut={handleLogOut} />
            ) : (
              <ShortMenu />
            )}
          </Box>
        )}
      </Box>
    </StyledHeader>
  )
};

export default NavBar