import { useState, useContext} from "react"
import { useNavigate } from "react-router-dom";
import NotificationContext from "context/NotificationContext";
import UserContext from "context/userContext";
import loginService from '../../services/login'
import { Box, Button, Form, FormField, TextInput } from 'grommet'
import { CenterBox, CenteredContainer } from "styles/styles";

const LoginForm = ({ users, loggedUserData, setLoggedUserData }) => {
  const [user, userDispatch] = useContext(UserContext)
  const [notification, notificationDispatch] = useContext(NotificationContext)

  const navigate = useNavigate();

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')

  const handleSubmit = async (event) => {
    event.preventDefault()

    try {
      const user = await loginService.login({
        username: username,
        password: password
      })

      notificationDispatch({ type: 'LOGIN', username })

      window.localStorage.setItem(
        'loggedRecipeappUser', JSON.stringify(user)
      )
      userDispatch({
        type: 'SET_USER',
        payload: user,
      })

      const userData = users.find(u => u.username === user.username);

      console.log('userdata', userData)
          setLoggedUserData(userData);
      
      setUsername('')
      setPassword('')
      navigate('/')
    }
    catch (error) {
      console.log(error)
      const message = error.message
      notificationDispatch({ type: 'LOGIN-ERROR', message})
    }
  }
  console.log('users', users)
  return (
    <CenteredContainer >
      <CenterBox >
        <Box margin="25px">
          <Form >
            <FormField username="username" htmlFor="text-input-id" label="Username" width="95%">
              <TextInput
                type="text"
                id='username'
                value={username}
                name="Username"
                onChange={({ target }) => setUsername(target.value)} />
            </FormField>

            <FormField password="password" htmlFor="text-input-id" label="Password" width="95%">
              <TextInput
                type="text"
                id='password'
                value={password}
                name="Password"
                onChange={({ target }) => setPassword(target.value)} />
            </FormField>

            <Box direction="row" gap="xsmall">
              <Button type="submit" primary label="Kirjaudu" size="xsmall" onClick={handleSubmit} />
              <Button type="submit" primary label="Uusi käyttäjä?" size="xsmall" onClick={() => navigate('/createuser')} />
            </Box>
          </Form>
        </Box>
      </CenterBox>
    </CenteredContainer>
  )
}

export default LoginForm
