import { Box, Button } from "grommet"

const IntroFilters = ({ recipe }) => {
  
  return (
    <Box margin={{ right: "30px" }} direction="row-responsive" gap="6px" justify="center" >

      {recipe.diet && recipe.diet.length > 0 ? (
        <Box width="auto">
          {recipe.diet.map((diet, index) => (
            <Button key={index} label={diet} size="medium" style={{ marginRight: '8px', marginBottom: '8px' }} />
          ))}
        </Box>
      ) : null}

      {recipe.main_ingredient && 
      <Box width="auto">
        <Button label={recipe.main_ingredient} size="medium" />
      </Box>}
      
      {recipe.category &&
      <Box width="auto">
        <Button label={recipe.category} size="medium" />
      </Box>}
      
    </Box>
  )
}

export default IntroFilters