import { Box, Button, Select, Text, TextInput } from "grommet"
import styled from "styled-components";
import { Cafeteria, Dashboard, Tip as Popover } from "grommet-icons"
import mainingredient from 'icons/main-ingredient.png'

const StyledTextInput = styled(TextInput).attrs(() => ({
  'aria-labelledby': 'search-icon',
}))``;

const Selectors2 = ({ category, setCategory, mainIngredient, setMainIngredient, recipespeed, handleReceptSpeed, isSmallScreen }) => {

  const mainingredientLogo = <img className="logo" src={mainingredient} alt="logo" width="23px" />
  
  return (
    <Box gap="5px">
      <Box margin={{ left: "15px" }} gap="5px" >
        <Select
          size="small"
          options={['alkupala', 'hampurilainen', 'juoma', 'jälkiruoka', 'keitto', 'leipä', 'lisuke', 'panos',
            'pasta', 'pizza', 'pääruoka', 'salaatti', 'sauce', 'scran', 'snack', 'suolainen leivottu']}
          value={category}
          onChange={({ option }) => setCategory(option)}
          icon={<Cafeteria id="dashboard-icon" />}
        />
        <Select
          size="small"
          options={['kana', 'kala ja äyriäiset', 'kasvikset', 'liha', 'maitotuotteet ja juusto', 'makkara ja jauheliha',
            'marjat tai hedelmät', 'viljat ja riisi']}
          value={mainIngredient}
          onChange={({ option }) => setMainIngredient(option)}
          icon={mainingredientLogo}
        />

      </Box>
      <Box direction="row">
        <Box width="100px" align="end" justify="center" margin={{ left: "15px" }}>
          <StyledTextInput
            size="small"
            placeholder="0-100"
            value={recipespeed}
            onChange={handleReceptSpeed}
            icon={<Dashboard id="dashboard-icon" />}
          />
        </Box>
      </Box>
    </Box>



  )
}


export default Selectors2