import { useMutation, useQueryClient, useQuery } from '@tanstack/react-query'
import NotificationContext from './context/NotificationContext'
import UserContext from 'context/userContext';
import NotificationPop from './query/NotificationQuery'

import { Grommet, Box } from 'grommet';
import { Routes, Route } from '../node_modules/react-router-dom/dist/index';

import NavBar from './Menu/NavBar';
import BottomBar from 'Menu/Footer';

import LoginForm from 'pages/LoginPage/LoginForm';
import CreateUser from 'pages/CreateuserPage/CreateUser';

import MainPage from 'pages/MainPage/MainPage';
import RecipesPage from 'pages/RecipesPage/RecipesPage';
import AddRecipePage from 'pages/AddRecipePage/AddRecipePage';
import SingleRecipe from 'pages/SingleRecipe/SingleRecipe';
import ProfilePage from 'pages/ProfilePage/ProfilePage';
import IntroductionPage from 'pages/IntroductionPage';

import { getRecipes, getAllAvatars } from './services/aws'
import { createRecipe } from './services/recipes'
import { createUser } from './services/users'
import recipeService from './services/recipes'
import userService from './services/users'
import awsService from './services/aws'

import { useEffect, useState, useContext } from 'react';
import { theme } from 'styles/theme';

const App = () => {

  const queryClient = useQueryClient()

  const [loggedUserData, setLoggedUserData] = useState(null)
  const [notification, notificationDispatch] = useContext(NotificationContext)
  const [user, userDispatch] = useContext(UserContext)

  const newRecipeMutation = useMutation({
    mutationFn: createRecipe,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['recipes'] })
      notificationDispatch({ type: 'CREATE' })
    },
    onError: (error) => {
      console.log(error)
      notificationDispatch({ type: 'CREATE-ERROR' })
    }
  })
  const newUserMutation = useMutation({
    mutationFn: createUser,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['users'] })
      notificationDispatch({ type: 'CREATE-USER' })
    },
    onError: (error) => {
      console.log(error)
      notificationDispatch({ type: 'CREATE-USER-ERROR', error })
    }
  })

  const recipes = useQuery({
    queryKey: ['recipes'],
    queryFn: getRecipes,
    retry: 1
  })

  const users = useQuery({
    queryKey: ['users'],
    queryFn: getAllAvatars,
    retry: 1
  })

  useEffect(() => {
    const fetchData = async () => {
      try {
        // // Fetch all users
        // const allUsers = await awsService.getAllAvatars();
        // console.log('allUSERS', allUsers)
        // setUsers(allUsers);

        // Check if there's a logged-in user
        const loggedUserJSON = window.localStorage.getItem('loggedRecipeappUser')
     
        if (loggedUserJSON) {
          const user = JSON.parse(loggedUserJSON)

          userDispatch({
            type: 'SET_USER',
            payload: { user },
          })
          recipeService.setToken(user.token);

        }
      } catch (error) {
        console.error('Error fetching users:', error);
      }
    };

    fetchData();
  }, []);

  if ( recipes.isLoading ) {
    return <div>loading data...</div>
  }

  if ( users.isLoading ) {
    return <div>loading data...</div>
  }

  const awsRecipeImageAdd = async (file) => {
    try {
      await awsService.create(file)
    }
    catch (error) {
      console.log(error)
    }
  }

  const handleRecipeAdd = async (recipeObject) => {
    const recipe_name = recipeObject.recipe_name
    const ingredients = recipeObject.ingredients
    const instructions = recipeObject.instructions
    const speed = recipeObject.speed
    const category = recipeObject.category
    const main_ingredient = recipeObject.main_ingredient
    const diet = recipeObject.diet
    const comments = recipeObject.comments
    const created = recipeObject.created
    const imageName = recipeObject.imageName

    newRecipeMutation.mutate({ recipe_name, ingredients, instructions, speed, category, main_ingredient, diet, comments, created, imageName })
  }

  const awsAvatarImageAdd = async (file) => {
    try {
      await awsService.createAvatar(file)
    }
    catch (error) {
      console.log(error)
    }
  }

  const handleUserAdd = async (newUser) => {
    newUserMutation.mutate({ newUser })
  }

  return (
    <Grommet theme={theme} full>
      <NotificationContext.Provider value={[notification, notificationDispatch]} >
        <UserContext.Provider value={[user, userDispatch]} >

          {notification &&
            <Box fill align="center" justify="center" style={{ position: 'absolute', zIndex: 1 }}>
              <NotificationPop />
            </Box>}

          <NavBar loggedUserData={loggedUserData} />
          <Routes>
            <Route path="/" element={<MainPage recipes={recipes.data} />} />
            <Route path="/signin" element={<LoginForm users={users.data} loggedUserData={loggedUserData} setLoggedUserData={setLoggedUserData} />} />
            <Route path="/createuser" element={<CreateUser handleUserAdd={handleUserAdd}
              handleImageAdd={awsAvatarImageAdd} />} />
            <Route path="/profile" element={<ProfilePage loggedUserData={loggedUserData} />} />
            <Route path="/us" element={<IntroductionPage />} />
            <Route path="/recipes" element={<RecipesPage recipes={recipes.data} />} />
            <Route path="/addrecipe" element={<AddRecipePage handleRecipeAdd={handleRecipeAdd}
              handleImageAdd={awsRecipeImageAdd} />} />

            {recipes.data?.map(recipe => (
              <Route
                key={recipe.id}
                path={`/recipes/${recipe.id}`}
                element={<SingleRecipe recipe={recipe} />}
              />
            ))}
          </Routes>
          <BottomBar />

        </UserContext.Provider>
      </NotificationContext.Provider>
    </Grommet>

  );
}

export default App;
